<template>
  <div class="py-4 px-5 bg-white rounded-20 border border-gray2">
    <div class="d-flex justify-content-center align-items-center position-relative" style="margin-bottom: 32px">
      <a class="position-absolute top-50 translate-middle-y"
          style="left: 7%"
          href="#"
          @click.prevent="selectMonth(true)">
        <img width="10" height="21" src="@/assets/stylesheets/icon/arrow_left.png" alt="arrow_back">
      </a>
      <div class="d-flex text-main">
        <p class="display-3 mb-0 me-3" style="font-weight: 900;">{{ month + 1 }}</p>
        <div class="align-self-center">
          <p class="mb-0 h4" style="font-weight: 900;">{{ monthNumToEn(month) }}</p>
          <span class="h6 text-main-light2" style="font-weight: 900; color: #66A9AE;">{{ year }}</span>
        </div>
      </div>
      <a class="position-absolute top-50 translate-middle-y"
          style="right: 7%"
          href="#" @click.prevent="selectMonth(false)">
        <img width="10" height="21" src="@/assets/stylesheets/icon/arrow_right.png" alt="arrow_forward">
      </a>
    </div>

    <!-- 月曆 -->
    <div class="scroll-content-none" style="max-height: 612px;">
      <table class="w-100" style="font-size: 12px;">
        <thead>
          <tr class="text-center">
            <th class="pb-5" style="font-weight: 600;" scope="col">Sunday</th>
            <th class="pb-5" style="font-weight: 600;" scope="col">Monday</th>
            <th class="pb-5" style="font-weight: 600;" scope="col">Tuesday</th>
            <th class="pb-5" style="font-weight: 600;" scope="col">Wednesday</th>
            <th class="pb-5" style="font-weight: 600;" scope="col">Thursday</th>
            <th class="pb-5" style="font-weight: 600;" scope="col">Friday</th>
            <th class="pb-5" style="font-weight: 600;" scope="col">Saturday</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(week, index) in calendarData" :key="index">
            <td :style="`width: calc(100% / ${7}); min-height: 126px;`"
              @click.stop="enterDateOfAppointment(item)"
              style="cursor: pointer;"
              class="p-1" v-for="(item, index) in week" :key="index">
              <div class="text-gray4 rounded-4 h-100 pb-3" style="min-height: 141px;"
                  :class="{
                    'bg-gray1' : item.day === 0 || item.day === 6,
                    'text-gray3' : !item.isCurrentMonth,
                    'border border-gray2' : item.date !== today.date || item.year !== today.year || item.month !== today.month || item.month !== month + 1 || item.year !== year,
                    'border border-main' : item.date === today.date && item.year === today.year && item.month === today.month && item.month === month + 1 && item.year === year,
                    }">
                <p class="mb-0 py-1 text-center border-bottom border-gray2">{{ item.date }}</p>
                <table class="appointmentList">
                  <thead>
                    <tr v-if="item.isCurrentMonth"
                        style="height: 24px; width: 24px;"
                        :class="{ 'text-gray3' : expirationJudgment(item) }">
                      <th v-for="item in selectedDoctor" :key="item.id" scope="col">{{ item.nickname }}</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td v-for="(amount, index) in item.morning" :key="index"
                      style="height: 24px; width: 24px;">
                        <button type="button" class="appointmentList-btn btn-morning"
                                @click.stop="enterDateOfAppointment(item, amount.clinicPeriod)"
                                :disabled="expirationJudgment(item)"
                          v-if="amount[calendarContent] !== 0 && filterQueryStatus.morning && filterQueryStatus.continuousOutpatientNum <= amount[calendarContent]">
                          {{ amount[calendarContent] }}
                        </button>
                      </td>
                    </tr>
                    <tr>
                      <td v-for="(amount, index) in item.afternoon" :key="index"
                      style="height: 24px; width: 24px;">
                        <button type="button" class="appointmentList-btn btn-afternoon"
                                @click.stop="enterDateOfAppointment(item, amount.clinicPeriod)"
                                :disabled="expirationJudgment(item)"
                          v-if="amount[calendarContent] !== 0 && filterQueryStatus.afternoon && filterQueryStatus.continuousOutpatientNum <= amount[calendarContent]">
                          {{ amount[calendarContent] }}
                        </button>
                      </td>
                    </tr>
                    <tr>
                      <td v-for="(amount, index) in item.evening" :key="index"
                      style="height: 24px; width: 24px;">
                        <button type="button" class="appointmentList-btn btn-evening"
                                @click.stop="enterDateOfAppointment(item, amount.clinicPeriod)"
                                :disabled="expirationJudgment(item)"
                          v-if="amount[calendarContent] !== 0 && filterQueryStatus.evening && filterQueryStatus.continuousOutpatientNum <= amount[calendarContent]">
                          {{ amount[calendarContent] }}
                        </button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
// import { mapGetters } from 'vuex';
// import { Modal } from 'bootstrap';
import { inject, toRefs, reactive } from 'vue';
export default {
  name: 'monthOfAppointment',
  setup() {
    const store = inject('store');
    const { appointmentState, timeSwitch, continuousOrReservedToggle } = store;

    const data = reactive({
      // aestheticians: [],
    });

    const enterDateOfAppointment = (item, clinicPeriod) => {
      if (expirationJudgment(item)) return null;
      if (item.isCurrentMonth) {
        appointmentState.date = item.date;
        appointmentState.appointmentContent = 'dateOfAppointment';
        appointmentState.scrollToTimePeriodString = clinicPeriod || null;
      }
    }

    /** 月份數字轉英文 */
    const monthNumToEn = (month) => {
      const monthArr = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
      return monthArr[month];
    }

    /** 月曆 - 過期判斷 */
    const expirationJudgment = (item) => {
      let time = new Date(item.year, item.month - 1, item.date).getTime();
      let now = new Date().getTime();

      if (item.date === appointmentState.today.date && item.year === appointmentState.today.year && item.month === appointmentState.today.month) {
        return false;
      }

      if (now < time) {
        return false;
      }
      
       return true;
    }

    /** 前後個月控制 */
    const selectMonth = (judge) => {
      const { year, month } = timeSwitch(appointmentState.year, appointmentState.month, judge);

      appointmentState.year = year;
      appointmentState.month = month;

      continuousOrReservedToggle(appointmentState.calendarContent);
    }

    return {
      data,
      monthNumToEn,
      expirationJudgment,
      selectMonth,
      enterDateOfAppointment,
      ...toRefs(appointmentState),
    }
  }
}
</script>

