                                       placeholder="部位"
                                       v-model="data.otherBodyParts[index].part">
                                <input type="text" class="form-control rounded-pill text-main-dark2 me-4"
                                       placeholder="大小"
                                       v-model="data.otherBodyParts[index].scope">
                                <input type="text" class="form-control rounded-pill text-main-dark2 me-4"
                                       placeholder="金額"
                                       v-model="data.otherBodyParts[index].price">
                                <a href="#" class="align-self-center"
                                            v-if="item !== data.otherBodyPartLength"
                                            @click.prevent="deleteOtherBodyPartLength(index)">
                                  <img src="@/assets/stylesheets/icon/--05.png" alt="" width="20" height="20">
                                </a>
                                <a href="#" class="align-self-center"
                                            v-if="item === data.otherBodyPartLength"
                                            @click.prevent="addOtherBodyPartLength()">
                                  <img src="@/assets/stylesheets/icon/+-04.png" alt="" width="20" height="20">
                                </a>
                              </div>
                            </div>
                        
                        </div>
                      </div>
                    </div>

                    <div class="col-12">
                      <div class="input-group">
                        <label class="input-group-text align-items-start text-main-dark2 mb-0 me-4 border-0 p-0 bg-transparent"
                               for="inputGroupSelect01"><span class="me-8">備</span><span>註</span></label>
                        <textarea class="form-control rounded-15" rows="3" v-model="data.postModalData.appointment.comment"></textarea>
                      </div>
                    </div>

                  </div>
                </div>

                <div class="d-flex justify-content-end mb-5" v-if="data.modelStatus === '新增約診'">
                  <button type="button" class="btn btn-alert-second rounded-pill me-2" data-bs-dismiss="modal">返 回</button>
                  <button type="button" class="btn btn-alert-main rounded-pill" @click="createAppointment()">確定新增</button>
                </div>

                <div class="d-flex justify-content-end mb-5" v-if="data.modelStatus === '編輯約診'">
                  <button type="button" class="btn btn-alert-second rounded-pill me-2" data-bs-dismiss="modal">返 回</button>
                  <button type="button" class="btn btn-alert-second rounded-pill me-2" @click="openCourseModal()">取消預約</button>
                  <button type="button" class="btn btn-alert-main rounded-pill" @click="toEditedOutpatient()">修改完成</button>
                </div>

                <CustomerTreatmentHistory :customerId="customerTreatmentHistoryCustomerId"></CustomerTreatmentHistory>

              </div>
            </div>
          </div>
        </div>

    <!-- 取消約診 modal -->
    <div class="modal fade" ref="courseModalRef">
      <div class="modal-dialog">
        <div class="modal-content">
          <h4 class="text-center mb-4">取 消 約 診</h4>
          <div class="rounded-top-15 bg-main-dark1 py-2 px-3">
            <p class="mb-0 text-white">{{ convertTimeFormatSlash(data.courseModalData.date) }}</p>
          </div>
          <div class="border border-main-dark1 rounded-bottom-15 mb-4">
            <div class="pt-3 px-5">
              <span class="me-2 mb-3 amis-badge amis-badge-second3" v-if="data.courseModalData">
                {{ clinicPeriodToCh(data.courseModalData.clinicPeriod) }}
              </span>
              <span class="amis-badge amis-badge-second3 me-2">
                <img class="me-2" src="@/assets/stylesheets/icon/icon-01.png" alt="icon" width="12.86" height="15">
                <span class="align-middle">{{ data.courseModalData.operatorName }}</span>
              </span>
              <span class="me-2 mb-3 amis-badge amis-badge-second3">
                項目 {{ data.courseModalData.courseName }} : {{ data.courseModalData.appointmentBodyPartsName }}
              </span>
            </div>

            <div class="py-3 px-5 border-top border-main-dark1">
              <div class="input-group">
                <label class="input-group-text text-main-dark2 mb-0 me-5 border-0 p-0 bg-transparent">取消原因 :</label>
                <input type="text" class="form-control rounded-pill text-main-dark2"
                        v-model="couseReason">
              </div>
            </div>
          </div>

          <div class="d-flex justify-content-end">
              <button type="button" class="btn btn-alert-second rounded-pill me-2"
                      @click="openEditOutpatient(data.courseModalData.id, data.courseModalData.time, data.courseModalData.customerId)">返 回</button>
              <button type="button"
                      class="btn btn-alert-main rounded-pill"
                      @click="toCourse(data.courseModalData.id)">確定取消</button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import { Modal } from 'bootstrap';
import { inject, toRefs, reactive, nextTick, ref, onMounted, computed, watch } from 'vue';

import CustomerTreatmentHistory from '@/components/CustomerTreatmentHistory.vue';

export default {
  name: 'dateOfAppointment',
  components: {
    CustomerTreatmentHistory,
  },
  setup() {
    const store = inject('store');
    const alertMessage = inject('alertMessage');

    const fetchFunction = inject('fetchFunction');

    const {
      fetchApi,
      getTokenFromCookie
    } = fetchFunction;

    const textConversionTool = inject('textConversionTool');

    const {
      convertTimeFormatDeleteSecond,
      convertTimeFormatSlash
    } = textConversionTool;

    const {
      appointmentState,
      getDaysOutpatientData,
      findAlternateByDate,
      continuousOrReservedToggle,
      getClinicPeriodLastTime,
      // convertTimeFormatSlash,
      clinicPeriodToCh,
      courseEnter,
      findAlternateByYearMonth,
      getUpdateAppointmentData,
     } = store;

    const { updateMessage } = alertMessage;

    const newAppointmentToSelectTypeRef = ref(null);
    const modalRef = ref(null);
    const courseModalRef = ref(null);

    let newAppointmentToSelectTypeModal;
    let modal;
    let courseModal;

    const data = reactive({
      focusIndex: null,
      stripCalendar: [],
      courseModalData: {},
      selectTime: '',
      modelStatus: '',
      appointmentType: [],
      selectedAppointmentType: {},
      otherBodyPartLength: 1,
      historyCourseWindow: false,
      modalData: {},
      postModalData: {
        appointment: {}
      },
      appointmentBodyParts: [],
      appointmentBodyPartsWindow: false,
      otherBodyParts: [],
    });

    /** 改變醫生或日期，去取時間 */
    const changedateAndOperator = async() => {

      const { departmentId, date, operator } = data.postModalData.appointment;

      const url = `/api/Outpatient/GetWhenCanReserveAppointment?departmentId=${departmentId}&date=${date}&operatorId=${operator}`;
      
      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      data.modalData.outpatientTimes = response.data;
    }

    const customerTreatmentHistory = ref([]);

    /** 約診類型決定欄位顯示 */
    const displayConditions = (id) => {
      switch (id) {
        case 'IV':
          return false;
        case 'OPD':
          return false;
        default:
          return true;
      }
    }

    /** 新增約診，確定新增 */
    const createAppointment = async() => {

      // if (data.selectedAppointmentType.id === 'IV') {
      //     data.appointmentBodyParts = [{id: 'UpperArm', name: '上手臂'}];
      //   }

      // if (data.selectedAppointmentType.id === 'OPD') {
      //   data.appointmentBodyParts = [{id: 'FullFace', name: '全臉'}];
      // }

      const time = data.modalData.outpatientTimes.find(item => item.time === data.postModalData.appointment.time);

      if (data.selectedAppointmentType.id !== 'Alternate') {
        data.postModalData.appointment.clinicPeriod = time.clinicPeriod; 
      }
      
      const postData = {
        outpatientId: data.selectedAppointmentType.id !== 'Alternate' ? time.outpatientId : null,
        appointment: data.postModalData.appointment,
        appointmentBodyParts: data.appointmentBodyParts.map(item => item.id),
        appointmentOtherParts: data.otherBodyParts
      }

      if (postData.appointment.operator === '') postData.appointment.operator = null;

      const url = `/api/Appointment`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'POST',
        body: JSON.stringify(postData),
      }

      const response = await fetchApi(url, options);

      updateMessage(response.message);

      if (response.statusCode >= 400) return null;

      if (appointmentState.appointmentContent === 'dateOfAppointment') {
        findAlternateByDate();
      } else {
        findAlternateByYearMonth();
      }

      continuousOrReservedToggle(appointmentState.calendarContent);
      appointmentState.selectedCustomer = {};
      modal.hide()
    }

    /** 條狀日期，選擇日期 */
    const selectDate = ({date, month, year}) => {

      appointmentState.date = date;
      appointmentState.month = month - 1;
      appointmentState.year = year;

      getDaysOutpatientData();
      findAlternateByDate();
    }

    /** 改變治療種類，治療項目和部位變為空值 */
    const changeCourseType = async() => {
      data.postModalData.appointment.courseId = '';
      data.appointmentBodyParts = [];
      data.modalData.courses = await getCourseOptions(data.postModalData.appointment.courseType);
    }

    /** 改變治療項目，部位變為空值 */
    const changeCourseId = async() => {
      data.appointmentBodyParts = [];
      data.modalData.bodyParts = await getBodyPart(data.postModalData.appointment.courseId);

      updateBasicDataAndFee(appointmentState.selectedCustomer.id, data.postModalData.appointment.courseId);
    }

    /** 刪除治療部位選項 */
    const deleteAppointmentBodyPartsItem = (item) => {
      if (item.id === 'Other') {
        data.otherBodyPartLength = 1;
        data.otherBodyParts = [];
      }

      let index = data.appointmentBodyParts.indexOf(item);
      data.appointmentBodyParts.splice(index, 1);
    }

    /** 新增其他欄位 */
    const addOtherBodyPartLength = () => {
      data.otherBodyPartLength++;
      data.otherBodyParts.push({
        part: "",
        scope: "",
        price: ""
      })
    }

    /** 刪除其他欄位 */
    const deleteOtherBodyPartLength = (index) => {
      data.otherBodyPartLength--;
      data.otherBodyParts.splice(index, 1);
    }

    /** 取得療程選項 */
    const getCourseOptions = async(courseType) => {

      const url = `/api/Course/GetCourseOptionsByType?courseTypeId=${courseType}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      return response.data;
    }

    /** 取得治療部位 */
    const getBodyPart = async(courseId) => {
      const url = `/api/BodyPart/GetBodyPartByCourse?courseId=${courseId}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      return response.data;
    }

    /** 開啟治療部位下拉選單 */
    const openAppointmentBodyPartsWindow = () => {
      if (data.appointmentBodyPartsWindow) return null;
      data.appointmentBodyPartsWindow = true;

      function close() {
        data.appointmentBodyPartsWindow = false;
        document.removeEventListener('click', close);
      }

      document.addEventListener('click', close);
    }

    /** 條狀日期，月轉英文 */
    const monthNumtoEn = (month) => {
      const arr = ["JAN", "FEB", "MAR", "APR", "MAY", "JUN", "JUL", "AUG", "SEP", "OCT", "NOV", "DEC"];
      return arr[month];
    }

    /** 取得新增約診的種類 */
    const getAppointmentType = async () => {
      
      const url = `/api/AppointmentType/GetDepartmentAppointmentType?departmentId=${appointmentState.department}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      data.appointmentType = response.data;
    }

    const updateBasicDataAndFeeData = ref([]);

    /** 更新病歷基本資料和收費 */
    async function updateBasicDataAndFee(customerId, courseId) {
      const url = `/api/MedicalRecord/GetOtherPartHistory?customerId=${customerId}&courseId=${courseId}&limit=5`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      updateBasicDataAndFeeData.value = response.data;
    }

    /** 選擇病歷基本資料和收費 */
    function selectUpdateBasicDataAndFeeData(item) {
      data.otherBodyPartLength++;
      data.otherBodyParts.push({
        part: item.part,
        scope: item.scope,
        price: item.price
      })
    }
    
    /** 開啟新增約診，選種類 madal */
    const openNewAppointmentToSelectTypeModal = async({id, time}, item) => {
      
      let judge = appointmentState.timeRange.findIndex(item1 => item1.time === time && item1.operatorId === id);

      const date = `${appointmentState.year}-${appointmentState.month + 1 <= 9 ?'0':''}${appointmentState.month + 1}-${appointmentState.date <= 9 ?'0':''}${appointmentState.date}`;
      data.modelStatus = '新增約診';

      data.postModalData = {
        appointment: {
          customerId: "",
          courseId: "",
          courseType: "Unknown",
          clinicPeriod: "Unknown",
          departmentId: appointmentState.department,
          date: date,
          time: null,
          type: "",
          operator: id,
          personInCharge: JSON.parse(localStorage.getItem('employee')).employeeId,
          timeRange: 1,
          anesthesiaType: "None",
          anesthesiaTime: null,
          comment: null,
        }
      };
      
      data.selectTime = time;

      console.log(appointmentState.selectedCustomer);

      if (Object.keys(appointmentState.selectedCustomer).length === 0) {
        updateMessage('請先至【客戶搜尋】 選擇欲約診客戶');

        return null;
      }

      await getAppointmentType();

      // 如果有主要約診或已經是跨時段，一般約診選項會被篩選掉
      if (item.normalAppointment || judge !== -1) {
        data.appointmentType = data.appointmentType.filter(item1 => item1.id !== "Normal");
      }

      // 如果有主要約診或已經是跨時段，一般約診選項會被篩選掉
      if (item.otherAppointment) {
        for(let i = 0; i < item.otherAppointment.length; i++) {
          data.appointmentType = data.appointmentType.filter(item1 => item1.id !== item.otherAppointment[i].appointmentType);
        }
      }

      data.selectedAppointmentType = data.appointmentType[0];

      newAppointmentToSelectTypeModal.show();
    }

    /** 返回月曆 */
    const enterMonthOfAppointment = async() => {
      appointmentState.appointmentContent = 'monthOfAppointment';

      continuousOrReservedToggle(appointmentState.calendarContent, true);
    }

    /** VIP 判斷 */
    const VIPJudge = (customerLevel) => {
      switch(customerLevel) {
        case 'Normal':
        return 'bg-white';
        case 'Gold':
        return 'bg-gold';
        case 'Purple':
        return 'bg-purple';
      }
    }

    const customerTreatmentHistoryCustomerId = ref(null);

    /** 約診的種類選擇完成，開啟填寫視窗 */
    const courseTypeSelectDone = async () => {
      
      // 治療紀錄，送入子元件 CustomerTreatmentHistory
      customerTreatmentHistoryCustomerId.value = appointmentState.selectedCustomer.id;

      data.appointmentBodyParts = [];
      data.otherBodyParts = [];

      data.postModalData.appointment.type = data.selectedAppointmentType.id;
      data.postModalData.appointment.customerId = appointmentState.selectedCustomer.id;

      data.postModalData.appointment.departmentId = appointmentState.department;

      if (data.selectedAppointmentType.id !== 'Alternate') {
        data.postModalData.appointment.time = data.selectTime;
      }
      
      const url = `/api/Appointment/GetInitCreateAppointmentData?departmentId=${appointmentState.department}&customerId=${appointmentState.selectedCustomer.id}&date=${data.postModalData.appointment.date}&operatorId=${data.postModalData.appointment.operator}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      data.modalData = response.data;

      // data.modalData.appointment.courseType = 'Unknown';

      data.modalData.courseTypes = data.modalData.courseTypes.filter(item => item.id !== 'OPD');

      if (data.selectedAppointmentType.id !== 'IV' || data.selectedAppointmentType.id !== 'OPD') {
        data.modalData.courseTypes = data.modalData.courseTypes.filter(item => item.id !== 'IV');
      }

      
      if (data.selectedAppointmentType.id === 'IV' || data.selectedAppointmentType.id === 'OPD') {
        data.modalData.courses = await getCourseOptions(data.selectedAppointmentType.id);
        if (data.selectedAppointmentType.id === 'IV') {
          data.postModalData.appointment.courseType = 'IV';
        }

        if (data.selectedAppointmentType.id === 'OPD') {
          data.postModalData.appointment.courseType = 'OPD';
        }
      }

      if (appointmentState.department === 'Beauty') {
        data.postModalData.appointment.courseType = data.modalData.courseTypes[0].id;
        data.modalData.courses = await getCourseOptions(data.modalData.courseTypes[0].id);
        data.postModalData.appointment.operator = null;
        // data.appointmentBodyParts = [{id: 'FullFace', name: '全臉'}];
      }

      data.modalData.courseTypes = data.modalData.courseTypes.filter(item => item.id !== 'IntravenousTherapy');
      
      newAppointmentToSelectTypeModal.hide();
      modal.show();
    }

    /** 取得條狀拖拉日曆 */
    const getStripCalendarData = (year, month) => {
      let time = new Date(year, month+1, 1);
      let arr = [];
      let dayEn = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

      time.setHours(time.getHours() - 1);    // 下個月 - 1 小時，目的是轉換成當月的最後一天 

      const allDate = time.getDate();

      for (let i = 1; i <= allDate; i++) {
        let index = new Date(year, month, i).getDay();
        arr.push({
          day: dayEn[index],
          date: i,
          month: month + 1,
          year: year,
        })
      }

      return arr;
    }

    /** 日期頭尾切換 */
    const timeSwitch = (year, month, judge) => {
      if (judge) {
        month -= 1;
      } else {
        month += 1;
      }

      if (month === 12) {
        month = 0;
        year+= 1;
      }

      if (month === -1) {
        month = 11;
        year -= 1;
      }

      return { year, month };
    }

    /** 條狀日期初始 */
    const stripCalendarInit = () => {
      let currentMonth = getStripCalendarData(appointmentState.year, appointmentState.month);

      currentMonth.forEach((item, index) => {
        if (item.date === appointmentState.date) {
          data.focusIndex = index;
        }
      })

      const nextLen = currentMonth.length - 10;
      const lastLen = currentMonth.length - nextLen;

      if (data.focusIndex <= lastLen) {
        const { year, month } = timeSwitch(appointmentState.year, appointmentState.month, true);
        
        let lastMonth = getStripCalendarData(year, month);
        currentMonth = lastMonth.concat(currentMonth);
      }

      if (data.focusIndex >= nextLen) {
        const { year, month } = timeSwitch(appointmentState.year, appointmentState.month, false);
        let nextMonth = getStripCalendarData(year, month);
        currentMonth = currentMonth.concat(nextMonth);
      }

      currentMonth.forEach((item, index) => {
        if (item.date === appointmentState.date && item.month === appointmentState.month + 1 && item.year === appointmentState.year) {
          data.focusIndex = index;
        }
      })

      data.stripCalendar = currentMonth;
      
      calendarListFocus(data.focusIndex);
    }

    /** 條狀拖拉日曆移動到目標 */
    const calendarListFocus = (num) => {
     
      const focus = num - 5;

      nextTick(() => {
        const items = document.querySelectorAll('.calendarList');
        let total = 0;

        for (let i = 0; i < focus; i++) {
        total += items[i].offsetWidth + 32;
      }

      const scroll = document.querySelector('.scroll');
      scroll.scrollTo({left: total, behavior: 'smooth'});
      });
    }

    /** 其他部位名稱轉陣列 */
    const otherPartNameToArr = (item) => {
      if(!item) return null;
      return item.split(',');
    }

    /** 開啟約診編輯 */
    const openEditOutpatient = async(id, time, customerId) => {
      courseModal.hide();
      if(editOutpatientTimeJudge(time)) return null;

      data.appointmentBodyParts = [];
      data.otherBodyParts = [];

      await getUpdateAppointmentData(id);

      data.modalData = JSON.parse(JSON.stringify(appointmentState.updateAppointmentData));

      data.selectedAppointmentType.id = data.modalData.appointment.type;

      data.modalData.courseTypes = data.modalData.courseTypes.filter(item => item.id !== 'OPD');

      if (data.selectedAppointmentType.id !== 'IV' || data.selectedAppointmentType.id !== 'OPD') {
        data.modalData.courseTypes = data.modalData.courseTypes.filter(item => item.id !== 'IV');
      }

      data.modelStatus = '編輯約診';
      
      const appointmentBodyParts = data.modalData.appointmentBodyParts;

      data.postModalData.appointment = data.modalData.appointment;

      updateBasicDataAndFee(customerId, data.postModalData.appointment.courseId);

      appointmentBodyParts.forEach(item1 => {
        data.modalData.bodyParts.forEach(item2 => {
          if (item1 === item2.id) {
            data.appointmentBodyParts.push(item2);
          }
        })
      })

      data.otherBodyParts = data.modalData.appointmentOtherParts;
      data.otherBodyPartLength = data.modalData.appointmentOtherParts.length;

      await getAppointmentType();

      // 治療紀錄，送入子元件 CustomerTreatmentHistory
      customerTreatmentHistoryCustomerId.value = customerId;

      data.modalData.courseTypes = data.modalData.courseTypes.filter(item => item.id !== 'IntravenousTherapy');

      data.selectedAppointmentType = data.appointmentType.find(item => item.id === data.modalData.appointment.type);
      modal.show();
    }

    /** 編輯修改完成 */
    const toEditedOutpatient = async() => {

      const time = data.modalData.outpatientTimes.find(item => item.time === data.postModalData.appointment.time);
      data.postModalData.appointment.clinicPeriod = time.clinicPeriod;

      const postData = {
        outpatientId: data.selectedAppointmentType.id !== 'Alternate' ? time.outpatientId : null,
        appointment: data.postModalData.appointment,
        appointmentBodyParts: data.appointmentBodyParts.map(item => item.id),
        appointmentOtherParts: data.otherBodyParts
      }

      const url = `/api/Appointment`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'PATCH',
        body: JSON.stringify(postData),
      }

      const response = await fetchApi(url, options);

      updateMessage(response.message);

      if (response.statusCode >= 400) return null;
      
      getDaysOutpatientData();
      findAlternateByDate();
      modal.hide()
    }

    /** modalRef 欄位顯示判斷 */
    const selectCourseDisabledJudge = () => {
      // 約診類型為點滴和健保就不顯示
      if (data.selectedAppointmentType.id === 'IV' || data.selectedAppointmentType.id === 'OPD') return false;

      // 治療項目空值，開啟
      if (data.postModalData.appointment.courseType === 'Unknown') return true;
    }

    /** 醫美部的背景色判斷 */
    const AestheticMedicineBgJudge = (item, {id, time}) => {

      // 抓出跨時段的格子
      const timeRangeJudge = appointmentState.timeRange.findIndex(item1 => item1.time === time && item1.operatorId === id);

      // 抓出跨時段的格子，判斷是否離診
      const leftJudge = appointmentState.timeRange.findIndex(item1 => item1.time === time && item1.left && item1.operatorId === id);
      
      // 如果沒開診
      if (!item.open) {
        return 'bg-gray2';
      }

      // 如果主要約診離診，就和跨時段一起變灰
      if (leftJudge !== -1) {
        return 'bg-gray3';
      }

      // 有主要約診 和 有跨時段
      if (item.normalAppointment || timeRangeJudge !== -1) {

        const left = item.normalAppointment ? item.normalAppointment.left : null;

        if (item.otherAppointment === null) {
          return left ? 'bg-gray3' : 'bg-green';
        }

        if (Array.isArray(item.otherAppointment)) {
          return item.otherAppointment.length === 0 && left ? 'bg-gray3' : 'bg-green';
        }
      }

      // 沒有主要約診
      if (!item.normalAppointment) {
        // 有次要約診
        if (item.otherAppointment) {
          const otherAppointmentAllLeftJudge = item.otherAppointment.every(item1 => item1.left === true);
          // 次要約診都離診
          if (otherAppointmentAllLeftJudge) {
            return 'bg-gray3';
          } else {
            return 'bg-green';
          }
        }
        // 沒有主要約診 和 沒有次要約診
        return 'bg-white';
      }
    }


    /** 美容部的背景色判斷 */
    const beautyBgJudge = (item, time) => {
      let judge = false;

      appointmentState.timeRange.forEach(item1 => {
        if(item1.time === time) {
          judge = true;
        }
      });

      if (!item.open) {
        return {'bg-gray2': true};
      }

      if (item.normalAppointment || judge || item.otherAppointment) {
        return {'bg-green': true};
      }

      if (!item.normalAppointment) {
        return {'bg-white': true};
      }
    }

    /** 顯示更多、細節 */
    const showDetails = (id) => {
      let dom = document.querySelector(`.index${id}`);
      dom.classList.remove('d-none');
    }

    /** 關閉更多、細節 */
    const closeDetails = (id) => {
      let dom = document.querySelector(`.index${id}`);
      dom.classList.add('d-none');
    }

    /** 開啟之前治療的方案 */
    const openHistoryCourseWindow = () => {
      if (data.historyCourseWindow) return null;
      data.historyCourseWindow = true;

      function close() {
        data.historyCourseWindow = false;
        document.removeEventListener('click', close);
      }

      document.addEventListener('click', close);
    }

    /** 如果其他被點，就開啟視窗 */
    const otherBodyPartJudge = computed(() => {
      let judge = false;
      if (data.appointmentBodyParts.length !== 0) {

        data.appointmentBodyParts.forEach(item => {

          if (item.id === 'Other') {
            judge = true;
          }
        })
      }

      
      return judge;
    });

    /** 若有更新值，判斷新值 true 和部位沒資料，就建一個預設 */
    watch(otherBodyPartJudge, (newValue) => {
    if (newValue && data.otherBodyParts.length === 0) {
        data.otherBodyPartLength = 1;
        data.otherBodyParts.push({
          part: "",
          scope: "",
          price: ""
        })
      }
  });

    const scrollToTimePeriod = () => {
      
      let dom;
      const scroll = document.querySelector('.scrollToTimePeriodBorder');
      
      if (appointmentState.scrollToTimePeriodString === 'Afternoon') {
        dom = document.querySelector('.scrollToAfternoon');
      }

      if (appointmentState.scrollToTimePeriodString === 'Evening') {
        dom = document.querySelector('.scrollToEvening');

        if (!appointmentState.filterQueryStatus.afternoon) {
          dom = document.querySelector('.scrollToAfternoon');
        }
      }

      if (!dom) return null;

      const targer = dom.offsetParent.offsetTop + dom.nextSibling.offsetTop - 70;
      // - 70，醫師表頭高度
      
      scroll.scrollTo({top: targer, behavior: 'smooth'});
    }

    /** 增加條狀拖拉日曆長度和下個顯示位置 */
    const calendarListMove = (judge) => {
      let len = data.stripCalendar.length;

      const nextLen = len - 10;
      const lastLen = len - nextLen;

      if (judge) {
        data.focusIndex = data.focusIndex - 6;
      } else {
        data.focusIndex = data.focusIndex + 6;
      }

      const focusItem = data.stripCalendar[data.focusIndex];

      if (data.focusIndex <= lastLen) {
        const { year, month } = timeSwitch(data.stripCalendar[data.focusIndex].year, data.stripCalendar[data.focusIndex].month - 1, true);

        let lastMonth = getStripCalendarData(year, month);

        data.stripCalendar = lastMonth.concat(data.stripCalendar);
      }

      if (data.focusIndex >= nextLen) {
        const { year, month } = timeSwitch(data.stripCalendar[data.focusIndex].year, data.stripCalendar[data.focusIndex].month - 1, false);
        let nextMonth = getStripCalendarData(year, month);
        data.stripCalendar = data.stripCalendar.concat(nextMonth);
      }

      data.stripCalendar.forEach((item, index) => {
        if (item.date === focusItem.date && item.month === focusItem.month && item.year === focusItem.year) {
          data.focusIndex = index;
        }
      })

      calendarListFocus(data.focusIndex);
    }

    /** 當前以前的時間不可以約診 */
    const outpatientTimeJudge = (time) => {

      const arr = time.split(':');

      let timestamp = new Date().getTime();
      let itemTimestamp = new Date(appointmentState.year, appointmentState.month, appointmentState.date, parseInt(arr[0]), parseInt(arr[1])).getTime();

      return timestamp < itemTimestamp ? true : false;
    }

    /** 今日以前的時間不可以編輯約診 */
    const editOutpatientTimeJudge = (time) => {

      const arr = time.split(':');

      const now = new Date();

      let timestamp = new Date(now.getFullYear(), now.getMonth(), now.getDate()).getTime();

      let itemTimestamp = new Date(appointmentState.year, appointmentState.month, appointmentState.date, parseInt(arr[0]), parseInt(arr[1])).getTime();

      return timestamp >= itemTimestamp ? true : false;
    }

    /** 開啟取消約診 modal */
    const openCourseModal = () => {
      let appointmentBodyPartsName = [];
      appointmentState.couseReason = '';

      const { appointment, courses, operators } = appointmentState.updateAppointmentData;

      appointmentState.updateAppointmentData.bodyParts.forEach(item1 => {
        appointmentState.updateAppointmentData.appointmentBodyParts.forEach(item2 => {
          if (item1.id === item2) {
            appointmentBodyPartsName.push(item1.name);
          }
        })
      })

      data.courseModalData = {
        date: appointment.date,
        time: appointment.time,
        operatorName: appointment.operator ? operators.find(item => item.operatorId === appointment.operator).operatorName : null,
        clinicPeriod: appointment.clinicPeriod,
        appointmentBodyPartsName: appointmentBodyPartsName.join('、'),
        courseName: courses.find(item => item.courseId === appointment.courseId).courseName,
        id: appointment.id,
        customerId: appointment.customerId,
      }

      modal.hide();
      courseModal.show();
    }

    /** 確認取消約診 modal */
    const toCourse = async(id) => {
      const res = await courseEnter(id);
      
      if (res.statusCode >= 400) {
        updateMessage(res.message);
        return null;
      }
      updateMessage(res.message);
      await getDaysOutpatientData();
      courseModal.hide();
    }

    /** 初始 */
    async function init() {
      stripCalendarInit();
    }

    init();

    onMounted(async () => {
      newAppointmentToSelectTypeModal = new Modal(newAppointmentToSelectTypeRef.value);
      modal = new Modal(modalRef.value);
      courseModal = new Modal(courseModalRef.value);
      await getDaysOutpatientData();
      await findAlternateByDate();
      await getClinicPeriodLastTime();
      scrollToTimePeriod();
    });

    return {
      data,
      monthNumtoEn,
      showDetails,
      closeDetails,
      enterMonthOfAppointment,
      calendarListMove,
      AestheticMedicineBgJudge,
      beautyBgJudge,
      convertTimeFormatDeleteSecond,
      otherPartNameToArr,
      openNewAppointmentToSelectTypeModal,
      // openNewAppointmentToSelectTypeModal2,
      openAppointmentBodyPartsWindow,
      changeCourseId,
      newAppointmentToSelectTypeRef,
      courseTypeSelectDone,
      modalRef,
      courseModalRef,
      createAppointment,
      otherBodyPartJudge,
      changeCourseType,
      deleteAppointmentBodyPartsItem,
      addOtherBodyPartLength,
      deleteOtherBodyPartLength,
      openHistoryCourseWindow,
      selectDate,
      displayConditions,
      outpatientTimeJudge,
      VIPJudge,
      openEditOutpatient,
      toEditedOutpatient,
      openCourseModal,
      convertTimeFormatSlash,
      clinicPeriodToCh,
      toCourse,
      courseEnter,
      selectCourseDisabledJudge,
      changedateAndOperator,
      scrollToTimePeriod,
      customerTreatmentHistory,

      // 更新病歷基本資料和收費
      updateBasicDataAndFeeData,
      selectUpdateBasicDataAndFeeData,

      customerTreatmentHistoryCustomerId,
      ...toRefs(appointmentState),
    }
  }
}
</script>

