<template>
  <div>
    <h4 class="text-center mb-4">治 療 紀 錄</h4>

      <div class="border border-main-dark1 rounded-15">
        <div class="row gx-0"
            v-for="(customerTreatmentHistoryItem, index) in customerTreatmentHistory"
            :key="customerTreatmentHistoryItem.appointmentDate"
            :class="{ 'border-bottom border-main-dark1' : index + 1 !== customerTreatmentHistory.length }">
          <div class="col-2">
            <div class="d-flex align-items-center justify-content-center h-100 border-end border-main-dark1">
              <span class="amis-badge amis-badge-second3">
                  {{ convertTimeFormatSlash(customerTreatmentHistoryItem.appointmentDate) }}
                </span>
            </div>
          </div>
          <div class="col-10">
            <div class="pt-3 px-4"
                v-for="(item, index) in customerTreatmentHistoryItem.items"
                :key="item"
                :class="{'border-bottom border-main-dark1': index + 1 !== customerTreatmentHistoryItem.items.length }">
              <span class="mb-3 me-2 amis-badge amis-badge-second3">
                  {{ item.clinicPeriodName }} {{ convertTimeFormatDeleteSecond(item.appointmentTime) }}
                </span>

                <span class="mb-3 me-2 amis-badge amis-badge-second3" v-if="item.operatorName">
                  <img class="me-1" src="@/assets/stylesheets/icon/icon-01.png" alt="icon" width="12.86" height="15">
                  {{ item.operatorName }}
                </span>

                <span class="mb-3 me-2 amis-badge amis-badge-second3" v-if="item.bodyParts">
                  項目 {{ item.bodyParts }}
                </span>

                <span class="mb-3 me-2 amis-badge amis-badge-second3 text-mark" v-if="item.return">
                  回診
                </span>

                <span class="mb-3 me-2 amis-badge amis-badge-second3 text-mark" v-if="item.cutInLine">
                  安插
                </span>

                <span class="mb-3 me-2 amis-badge amis-badge-second3" v-if="item.AnesthesiaTime">
                  麻醉 {{ item.AnesthesiaTime }}
                </span>
            </div>

          </div>
        </div>

      </div>
  </div>
</template>

<script>
import { ref, inject, watch } from 'vue';

export default {
  props: ['customerId'],
  setup(props) {

    const fetchFunction = inject('fetchFunction');

    const {
      fetchApi,
      getTokenFromCookie
    } = fetchFunction;

    const textConversionTool = inject('textConversionTool');

    const {
      convertTimeFormatSlash,
      convertTimeFormatDeleteSecond,
    } = textConversionTool;

    const customerTreatmentHistory = ref([]);

    watch(() => props.customerId, async(newValue) => {
      await getCustomerTreatmentHistory(newValue);
    },{
      immediate: true
    })

    /** 取得客戶治療紀錄 */
    async function getCustomerTreatmentHistory(id) {
      const url = `/api/MedicalRecord/GetCustomerTreatmentHistory?customerId=${id}&limit=10`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      console.log(response);

      customerTreatmentHistoryDataCollation(response.data);
    }

    /** 客戶治療紀錄資料整理 */
    function customerTreatmentHistoryDataCollation(response) {

      let data = [];

      response.forEach(itemA => {
        if (!data.some(itemB => itemA.appointmentDate === itemB.appointmentDate)) {
          data.push({
            appointmentDate: itemA.appointmentDate,
            items: []
          });
        }
      })

      data.forEach(itemA => {
        response.forEach(itemB=> {
          if (itemA.appointmentDate === itemB.appointmentDate) {
            itemA.items.push(itemB);
          }
        })
      })

      customerTreatmentHistory.value = data;
    }

    return {
      customerTreatmentHistory,
      convertTimeFormatSlash,
      convertTimeFormatDeleteSecond,
    }
  }
}
</script>