                                width: 1%;
                                min-width: 0;">
                      <a href="#" class="position-absolute" style="top: 4px;"
                        v-if="updateBasicDataAndFeeData.length !== 0"
                        @click.prevent.stop="openHistoryCourseWindow()">
                        <img src="@/assets/stylesheets/icon/TIME-02.png" alt="" width="20" height="20">
                      </a>

                      <div class="position-absolute" v-show="data.historyCourseWindow"
                            style="min-width: 223px; z-index: 10; left: 28px;">
                        <div class="bg-gray1 border border-gray3 rounded-15">
                          <table class="otherBodyPartTable text-main-dark1">
                            <thead>
                              <tr>
                                <th>其他部分</th>
                                <th>大小</th>
                                <th>金額</th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr v-for="item in updateBasicDataAndFeeData" :key="item"
                                  @click="selectUpdateBasicDataAndFeeData(item)">
                                <td>{{ item.part }}</td>
                                <td>{{ item.scope }} cm</td>
                                <td>{{ item.price }}</td>
                              </tr>
                            </tbody>
                          </table>
                        </div>
                      </div>
                      
                        <div class="d-inline-block ms-10">
                          <div class="input-group mb-3" v-for="(item, index) in data.otherBodyPartLength" :key="`key${item}`">
                            <span class="input-group-text text-main-dark2 mb-0 me-4 border-0 p-0 bg-transparent">其他</span>
                            <input type="text" class="form-control rounded-pill text-main-dark2 me-4"
                                    placeholder="部位"
                                    v-model="data.otherBodyParts[index].part">
                            <input type="text" class="form-control rounded-pill text-main-dark2 me-4"
                                    placeholder="大小"
                                    v-model="data.otherBodyParts[index].scope">
                            <input type="text" class="form-control rounded-pill text-main-dark2 me-4"
                                    placeholder="金額"
                                    v-model="data.otherBodyParts[index].price">
                            <a href="#" class="align-self-center"
                                        v-if="item !== data.otherBodyPartLength"
                                        @click.prevent="deleteOtherBodyPartLength(index)">
                              <img src="@/assets/stylesheets/icon/--05.png" alt="" width="20" height="20">
                            </a>
                            <a href="#" class="align-self-center"
                                        v-if="item === data.otherBodyPartLength"
                                        @click.prevent="addOtherBodyPartLength()">
                              <img src="@/assets/stylesheets/icon/+-04.png" alt="" width="20" height="20">
                            </a>
                          </div>
                        </div>
                    
                    </div>
                  </div>
                </div>

                <div class="col-12">
                  <div class="input-group">
                    <label class="input-group-text align-items-start text-main-dark2 mb-0 me-4 border-0 p-0 bg-transparent"
                            for="inputGroupSelect01"><span class="me-8">備</span><span>註</span></label>
                    <textarea class="form-control rounded-15" rows="3" v-model="data.postModalData.appointment.comment"></textarea>
                  </div>
                </div>

              </div>
            </div>

            <div class="d-flex justify-content-end mb-5" v-if="data.modelStatus === '新增約診'">
              <button type="button" class="btn btn-alert-second rounded-pill me-2" data-bs-dismiss="modal">返 回</button>
              <button type="button" class="btn btn-alert-main rounded-pill" @click="createAppointment()">確定新增</button>
            </div>

            <div class="d-flex justify-content-end mb-5" v-if="data.modelStatus === '編輯約診'">
              <button type="button" class="btn btn-alert-second rounded-pill me-2" @click="returnToAppointmentSearch()">返 回</button>
              <!-- <button type="button" class="btn btn-alert-second rounded-pill me-2" @click="openCourseModal()">取消預約</button> -->
              <button type="button" class="btn btn-alert-main rounded-pill" @click="toEditedOutpatient()">修改完成</button>
            </div>

            <CustomerTreatmentHistory :customerId="customerTreatmentHistoryCustomerId"></CustomerTreatmentHistory>

          </div>
        </div>
      </div>
    </div>

    <!-- 取消約診 modal -->
    <div class="modal fade" ref="courseAppointmentModalRef">
      <div class="modal-dialog">
        <div class="modal-content">
          <h4 class="text-center mb-4">取 消 約 診</h4>
          <div class="rounded-top-15 bg-main-dark1 py-2 px-3">
            <p class="mb-0 text-white">{{ convertTimeFormatSlash(data.courseModalData.date) }}</p>
          </div>
          <div class="border border-main-dark1 rounded-bottom-15 mb-4">
            <div class="pt-3 px-5">
              <span class="me-2 mb-3 amis-badge amis-badge-second3" v-if="data.courseModalData">
                {{ clinicPeriodToCh(data.courseModalData.clinicPeriod) }} {{ data.courseModalData.time }}
              </span>
              <span class="amis-badge amis-badge-second3 me-2">
                <img class="me-2" src="@/assets/stylesheets/icon/icon-01.png" alt="icon" width="12.86" height="15">
                <span class="align-middle">{{ data.courseModalData.operatorName }}</span>
              </span>
              <span class="me-2 mb-3 amis-badge amis-badge-second3">
                項目 {{ data.courseModalData.courseName }} : {{ data.courseModalData.appointmentBodyPartsName }}
              </span>
            </div>

            <div class="py-3 px-5 border-top border-main-dark1">
              <div class="input-group">
                <label class="input-group-text text-main-dark2 mb-0 me-5 border-0 p-0 bg-transparent">取消原因 :</label>
                <input type="text" class="form-control rounded-pill text-main-dark2"
                        v-model="couseReason">
              </div>
            </div>
          </div>

          <div class="d-flex justify-content-end">
              <button type="button" class="btn btn-alert-second rounded-pill me-2"
                      @click="returnToAppointmentSearch">返 回</button>
              <button type="button"
                      class="btn btn-alert-main rounded-pill"
                      @click="toCourse(data.courseModalData.id)">確定取消</button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import { inject, toRefs, reactive, ref, onMounted, computed, watch } from 'vue';
import { Modal } from 'bootstrap';

import dateOfAppointment from './dateOfAppointment';
import monthOfAppointment from './monthOfAppointment';
import CustomerTreatmentHistory from '@/components/CustomerTreatmentHistory.vue'

export default {
  name: 'appointmentIndex',
  components: {
    monthOfAppointment,
    dateOfAppointment,
    CustomerTreatmentHistory,
  },
  setup() {
    const store = inject('store');
    const accordions = inject('accordion');

    const {
      appointmentState,
      getDoctors,
      resetSelectedDoctor,
      getCurrentMonth,
      getDoctorContinuousOutpatientData,
      continuousOrReservedToggle,
      findAlternateByYearMonth,
      clinicPeriodToCh,
      // convertTimeFormatDeleteSecond,
      // convertTimeFormatSlash,
      departmentToggle,
      getDaysOutpatientData,
      courseEnter,
      findAlternateByDate
    } = store;

    const {
      accordionToggle,
    } = accordions;

    const fetchFunction = inject('fetchFunction');

    const textConversionTool = inject('textConversionTool');

    const {
      convertTimeFormatDeleteSecond,
      convertTimeFormatSlash
    } = textConversionTool;

    const {
      fetchApi,
      getTokenFromCookie
    } = fetchFunction;

    const Employee = inject('Employee');

    const {
      employeeState,
      getBeauticianAAByActiveStatus
    } = Employee;

    const {
      beauticianAAData
      } = toRefs(employeeState);

    const customerListRef = ref(null);
    const createCustomerRef = ref(null);
    const appointmentSearchRef = ref(null);
    const editAlternateRef = ref(null);
    const successModalRef = ref(null);

    const modalRef = ref(null);

    const courseModalRef = ref(null);
    const courseAppointmentModalRef = ref(null);

    const alertMessage = inject('alertMessage');

    const { updateMessage } = alertMessage;

    let customerListModal;
    let createCustomerModal;
    let appointmentSearchModal;
    let editAlternateModal;
    let successModal;

    let courseModal;
    let modal;
    let courseAppointmentModal;

    const data = reactive({
      customer: {
        name: '',
        tel: '',
        birth: '',
        beautician: '',
      },
      timeRange: 1,
      continuousOrReservedStatus: 'continuousOutpatientAmount',
      appointmenttime: {},
      postModalData: {
        appointment: {
        }
      },
      appointmentData: [],
      appointmentBodyPartsWindow: false,
      historyCourseWindow: false,
      // modal
      modalSelectedCustomer: {},
      selectedAppointmentType: '',
      // customerListModal: null,
      customerListModalBtn: true,
      customerListModalData: [],
      courseModalData: {},
      createCustomerModalSuccess: false,
      editAlternateModalData: {},
      appointmentBodyParts: [],
      otherBodyPartLength: 1,
      modalData: {},
      originalData: {},
      modelStatus: '編輯約診',
      otherBodyParts: [
        {
          part: "",
          scope: "",
          price: ""
        }
      ],
      // modal
      departmentId: '',
      alternateTime: [],

      successModaldata: {},
    });

    async function init() {
      await getDoctors();
      getCurrentMonth();
      resetSelectedDoctor();
      await getDoctorContinuousOutpatientData(true);

      // 顯示候補名單(月)
      findAlternateByYearMonth();

      await getBeauticianAAByActiveStatus();
    }

    init();

    const customerTreatmentHistoryCustomerId = ref(null);

    /** 醫師陣容 - 選擇醫師頭像 */
    const showOrHideDoctorData = async (item1) => {
      appointmentState.doctorData.forEach(item2 => {
        if (item1.id === item2.id) {
          if (item1.select) {
            if (appointmentState.selectedDoctor.length === 1) {
              // 最少要 1 位
              updateMessage('最少要 1 位醫生');
              return null;
            }
            item1.select = false;
          } else {
            // 最多選 4 位
            if (appointmentState.selectedDoctor.length >= 4) {
              updateMessage('最多選 4 位醫生');
              return null;
            }
            item1.select = true;
          }
        }
      })

      resetSelectedDoctor();

      if (appointmentState.appointmentContent === 'monthOfAppointment') {
        continuousOrReservedToggle(appointmentState.calendarContent);
        findAlternateByYearMonth();
      }

      if (appointmentState.appointmentContent === 'dateOfAppointment') {
        await getDaysOutpatientData();
        findAlternateByDate();
      }
    }

    /** 取最新過的資料 */
    const getNewAlternateData = async(id) => {
      
      const url = `/api/Appointment/GetInitUpdateAppointmentData?id=${id}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      data.originalData = response.data;
    }

    /** 開啟編輯候補 Modal */
    const openEditAlternateModal = async(item) => {
      await updateBasicDataAndFee(item.customerId, item.courseId);
      data.editAlternateModalData = {};
      data.appointmentBodyParts = [];
      successModal.hide();

      // 治療紀錄，送入子元件 CustomerTreatmentHistory
      customerTreatmentHistoryCustomerId.value = item.customerId;

      await getNewAlternateData(item.id);

      data.departmentId = item.departmentId;

      data.editAlternateModalData = JSON.parse(JSON.stringify(data.originalData));

      const { appointmentBodyParts } = data.originalData;

      const len = data.editAlternateModalData.appointmentOtherParts.length;

      // 自行填寫的其他部位，預設 1
      data.otherBodyPartLength = len !== 0 ? len : 1;
      data.otherBodyParts = data.editAlternateModalData.appointmentOtherParts;
        
      // 用治療部位 id 找出治療部位名稱
      const bodyPart = await getBodyPart(data.editAlternateModalData.appointment.courseId);

      appointmentBodyParts.forEach(item1 => {
        bodyPart.forEach(item2 => {
          if (item1 === item2.id) {
            data.appointmentBodyParts.push(item2);
          }
        })
      })

      // 將點滴篩選掉
      data.editAlternateModalData.courseTypes = data.editAlternateModalData.courseTypes.filter(item => item.id !== "IntravenousTherapy");

      editAlternateModal.show();
    }

    /** 開啟取消約診 modal */
    const openAppointmentCourseModal = (item) => {
      // let appointmentBodyPartsName = [];
      appointmentState.couseReason = '';

      const { operatorName, time, date, clinicPeriod, appointmentCoursePartName, courseName, id } = item;

      // data.originalData.bodyParts.forEach(item1 => {
      //   data.originalData.appointmentBodyParts.forEach(item2 => {
      //     if (item1.id === item2) {
      //       appointmentBodyPartsName.push(item1.name);
      //     }
      //   })
      // })

      data.courseModalData = {
        date: date,
        time: time,
        operatorName: operatorName,
        clinicPeriod: clinicPeriod,
        appointmentBodyPartsName: appointmentCoursePartName,
        courseName: courseName,
        id: id,
      }

      appointmentSearchModal.hide();
      courseAppointmentModal.show();
    }

    const returnToAppointmentSearch = () => {
      modal.hide();
      courseAppointmentModal.hide();
      appointmentSearchModal.show();
    }

    const displayConditions = (id) => {
      switch (id) {
        case 'IV':
          return false;
        case 'OPD':
          return false;
        default:
          return true;
      }
    }

    const selectCourseDisabledJudge = () => {
      if (data.selectedAppointmentType.id === 'IV' || data.selectedAppointmentType.id === 'OPD') return false;

      if (data.postModalData.appointment.courseType === '') return true;
    }

    const selectCourseDisabledJudge2 = () => {
      if (data.selectedAppointmentType.id === 'IV' || data.selectedAppointmentType.id === 'OPD') return false;

      if (data.editAlternateModalData.appointment.courseType === 'Unknown') return true;
    }

    /** 客戶查詢 - 開啟 modal (約診的客戶查詢 和 預約查詢) */
    const openCustomerListModal = async(judge) => {
      const reg = /\S/;
      const name = reg.test(data.customer.name);
      const tel = reg.test(data.customer.tel);
      const birth = reg.test(data.customer.birth);
      if (!name && !tel && !birth) {
        updateMessage('請至少提供客戶的姓名、生日、電話之中的一項資料');
        return null;
      }

      data.modalSelectedCustomer = {};
      data.customerListModalBtn = judge;

      const url = `/api/Customer/GetByNameAndBirthdayAndCellPhone?name=${data.customer.name}&birthday=${data.customer.birth}&cellPhone=${data.customer.tel}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      updateMessage(response.message);

      if (response.statusCode >= 400) return null;

      if (response.data.length === 0) updateMessage('沒有資料');

      data.customerListModalData = response.data;

      customerListModal.show();
    }

    /** 開啟約診編輯 */
    const openEditOutpatient = async(item) => {
      await updateBasicDataAndFee(item.customerId, item.courseId);
      data.appointmentBodyParts = [];
      data.otherBodyParts = [];

      // 治療紀錄，送入子元件 CustomerTreatmentHistory
      customerTreatmentHistoryCustomerId.value = item.customerId;
      await getNewAlternateData(item.id);

      data.modalData = JSON.parse(JSON.stringify(data.originalData));

      data.modelStatus = '編輯約診';

      const appointmentBodyParts = data.modalData.appointmentBodyParts;

      data.postModalData.appointment = data.modalData.appointment;
      appointmentBodyParts.forEach(item1 => {
        data.modalData.bodyParts.forEach(item2 => {
          if (item1 === item2.id) {
            data.appointmentBodyParts.push(item2);
          }
        })
      })


      data.departmentId = data.modalData.appointment.departmentId;
      data.otherBodyPartLength = data.modalData.appointmentOtherParts.length;
      data.otherBodyParts = data.modalData.appointmentOtherParts;

      await getAppointmentType();
      
      data.selectedAppointmentType = data.appointmentType.find(item => item.id === data.modalData.appointment.type);

      data.modalData.courseTypes = data.modalData.courseTypes.filter(item => item.id !== 'IntravenousTherapy');

      appointmentSearchModal.hide();
      modal.show();
    }

    /** 預約查詢 - 修改完成 */
    const toEditedOutpatient = async() => {

      const postData = {
        outpatientId: null,
        appointment: data.postModalData.appointment,
        appointmentBodyParts: data.appointmentBodyParts.map(item => item.id),
        appointmentOtherParts: data.otherBodyParts
      }

      const { type } = postData.appointment;

      if (type !== 'Alternate') {
        postData.outpatientId = data.modalData.outpatientTimes.find(item => item.time === data.postModalData.appointment.time).outpatientId;
      }

      const url = `/api/Appointment`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'PATCH',
        body: JSON.stringify(postData),
      }

      const response = await fetchApi(url, options);

      updateMessage(response.message);

      if (response.statusCode >= 400) return null;

      appointmentSearch();
      modal.hide()
    }

    /** 取約診類型 */
    const getAppointmentType = async() => {

      const url = `/api/AppointmentType/GetDepartmentAppointmentType?departmentId=${appointmentState.department}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      data.appointmentType = response.data;
    }

    const changeContinuousOrReservedStatus = () => {
      continuousOrReservedToggle(data.continuousOrReservedStatus);
    }

    const continuousOutpatientNum = computed({
      get: () => {
        return appointmentState.filterQueryStatus.continuousOutpatientNum;
      },
      set: (value) => {
        appointmentState.filterQueryStatus.continuousOutpatientNum = value;
        continuousOrReservedToggle(data.continuousOrReservedStatus, false);
      }
    });

    /** 如果其他被點，就開啟視窗 */
    const otherBodyPartJudge = computed(() => {
      let judge = false;
      if (data.appointmentBodyParts.length !== 0) {

        data.appointmentBodyParts.forEach(item => {
          if (item.id === 'Other') {
            judge = true;
          }
        })
      }

      
      return judge;
    });

    watch(otherBodyPartJudge, (newValue) => {
      if (newValue && data.otherBodyParts.length === 0) {
          data.otherBodyPartLength = 1;
          data.otherBodyParts.push({
            part: "",
            scope: "",
            price: ""
          })
        }
    });

    const toCourse = async(id) => {
      const res = await courseEnter(id);

      updateMessage(res.message);

      if (res.statusCode >= 400) return null;

      if (appointmentState.appointmentContent === 'dateOfAppointment') {
        await findAlternateByDate();
      } else {
        await findAlternateByYearMonth();
      }
      continuousOrReservedToggle(appointmentState.calendarContent);
      courseAppointmentModal.hide();
      appointmentSearch();
    }

    const changeCourseId = async() => {
      data.appointmentBodyParts = [];
      data.otherBodyParts = [];
      data.editAlternateModalData.bodyParts = await getBodyPart(data.editAlternateModalData.appointment.courseId);
    }

    const changeCourseId2 = async() => {
      data.appointmentBodyParts = [];
      data.otherBodyParts = [];
      data.modalData.bodyParts = await getBodyPart(data.editAlternateModalData.appointment.courseId);
    }

    /** 候補 - 選擇時段 */
    const selecttime = async() => {

      const { departmentId, clinicPeriod, date, operator } = data.editAlternateModalData.appointment;
      // 查找時段
      const url = `/api/Outpatient/GetWhenCanReserveNormalAppointment?departmentId=${departmentId}&clinicPeriod=${clinicPeriod}&date=${date}&operatorId=${operator}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      data.alternateTime = response.data;

      if(data.alternateTime.length === 0) updateMessage('沒有時段');

      data.appointmenttime = data.alternateTime[0];

      openSuccessModal();
    }

    /** 開啟候補成功 modal */
    const openSuccessModal = async() => {

      editAlternateModal.hide();

      let appointmentBodyPartsName = [];
      const { appointment, courses, bodyParts } = data.editAlternateModalData;

      bodyParts.forEach(item1 => {
        data.appointmentBodyParts.forEach(item2 => {
          if (item1.id === item2.id) {
            appointmentBodyPartsName.push(item1.name);
          }
        })
      })

      data.successModaldata = {
        appointmentBodyPartsName: appointmentBodyPartsName.join('、'),
        operatorName: data.editAlternateModalData.operators.find(item => item.operatorId === data.editAlternateModalData.appointment.operator).operatorName,
        date: appointment.date,
        clinicPeriod: appointment.clinicPeriod,
        courseName: courses.find(item => item.courseId === appointment.courseId).courseName,
      }

      successModal.show();
    }

    const openCourseModal = async(id) => {
      await getNewAlternateData(id);

      const { appointment, bodyParts, operators, courses, appointmentBodyParts } = data.originalData;

      const appointmentBodyPartsName = appointmentBodyParts.map(itemA => {
        return bodyParts.find(itemB => itemA === itemB.id).name;
      })

      data.courseModalData = {
        // title: '取 消 候 補',
        appointmentBodyPartsName: appointmentBodyPartsName.join('、'),
        courseName: courses.find(item => item.courseId === appointment.courseId).courseName,
        operatorName: operators.find(item => item.operatorId === appointment.operator).operatorName,
        clinicPeriod: appointment.clinicPeriod,
        date: appointment.date,
        id: appointment.id,
      }

      courseModal.show();
    }

    /** 確認取消候補 modal */
    const toCourseAlternate = async(id) => {
      await courseEnter(id);
      if (appointmentState.appointmentContent === 'dateOfAppointment') {
        await findAlternateByDate();
      } else {
        await findAlternateByYearMonth();
      }
      courseModal.hide();
    }

    /** 候補 - 改變預約療程選項 */
    const changeCourseType = async() => {
      data.editAlternateModalData.appointment.courseId = '';
      data.appointmentBodyParts = [];
      data.otherBodyParts = [];
      data.editAlternateModalData.courses = await getCourseOptions(data.editAlternateModalData.appointment.courseType);
    }

    /** 取得療程選項 */
    const getCourseOptions = async(courseType) => {
      
      const url = `/api/Course/GetCourseOptionsByType?courseTypeId=${courseType}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      if (response.statusCode >= 400) return null;

      return response.data;
    }

    /** 開啟治療部位下拉選單 */
    const openAppointmentBodyPartsWindow = () => {
      if (data.appointmentBodyPartsWindow) return null;
      data.appointmentBodyPartsWindow = true;

      function close() {
        data.appointmentBodyPartsWindow = false;
        document.removeEventListener('click', close);
      }

      document.addEventListener('click', close);
    }

    /** 刪除治療部位選項 */
    const deleteAppointmentBodyPartsItem = (item) => {
      if (item.id === 'Other') {
        data.otherBodyPartLength = 1;
        data.otherBodyParts = [];
      }
      let index = data.appointmentBodyParts.indexOf(item);
      data.appointmentBodyParts.splice(index, 1);
    }

    /** 刪除其他欄位 */
    const deleteOtherBodyPartLength = (index) => {
      data.otherBodyPartLength--;
      data.otherBodyParts.splice(index, 1);
    }

    /** 新增其他欄位 */
    const addOtherBodyPartLength = () => {
      data.otherBodyPartLength++;
      data.otherBodyParts.push({
        part: "",
        scope: "",
        price: ""
      })
    }

    /** 客戶搜尋 - 約診 - 選擇客戶 */
    const selectCustomer = () => {

      if (!data.modalSelectedCustomer.id) {
        updateMessage('請選擇客戶');
        return null;
      }

      appointmentState.selectedCustomer = data.modalSelectedCustomer;

      console.log(data.modalSelectedCustomer);

      customerListModal.hide();
      createCustomerModal.hide();
    }

    /** 客戶搜尋 - 預約紀錄 */
    const appointmentSearch = async() => {

      if (!data.modalSelectedCustomer.id) {
        updateMessage('請選擇客戶');
        return null;
      }

      const url = `/api/Appointment/GetByCustomerFromToday?customerId=${data.modalSelectedCustomer.id}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      if (response.statusCode >= 400) return null;

      data.appointmentData = response.data;

      customerListModal.hide();

      // 治療紀錄，送入子元件 CustomerTreatmentHistory
      customerTreatmentHistoryCustomerId.value = data.modalSelectedCustomer.id;

      appointmentSearchModal.show();
    }

    /** 候補名單 - 確定更新完成 */
    const editAlternateEnter = async() => {
      const url = `/api/Appointment/UpdateAlternate`;

      const appointmentOtherParts = data.otherBodyParts;
      const appointmentBodyParts = data.appointmentBodyParts.map(item => item.id);
      const { appointment } = data.editAlternateModalData;

      const putData = {
        outpatientId: null,
        appointment,
        appointmentBodyParts,
        appointmentOtherParts
      }

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'PATCH',
        body: JSON.stringify(putData),
      }

      const response = await fetchApi(url, options);

      updateMessage(response.message);

      if (response.statusCode >= 400) return null;

      if (appointmentState.appointmentContent === 'dateOfAppointment') {
        findAlternateByDate();
      } else {
        findAlternateByYearMonth();
      }
      editAlternateModal.hide();
    }

    /** 輸入中文就移除中文 */
    const numberGuards = (value) => {
      const num = data.customer[value];
      const reg = /^[0-9-]{1,20}$/;
      
      if (!reg.test(num)) {
        data.customer[value] = data.customer[value].replace(/.$/, '');
        return null;
      }
    }

    /** 開啟之前治療的方案 */
    const openHistoryCourseWindow = () => {
      if (data.historyCourseWindow) return null;
      data.historyCourseWindow = true;

      function close() {
        data.historyCourseWindow = false;
        document.removeEventListener('click', close);
      }

      document.addEventListener('click', close);
    }

    /** 客戶查詢 - 開啟建立客戶資料 Modal */
    const openCreateCustomerModal = () => {
      const reg = /\S/;
      const telReg = /^[0-9-]{1,20}$/;
      const birthReg = /^[0-9]{7,8}$/;
      const name = reg.test(data.customer.name);
      const tel = telReg.test(data.customer.tel);
      const birth = birthReg.test(data.customer.birth);
      
      if (!name || !tel || !birth) {
        updateMessage('請提供完整資料');
        return null;
      }
      data.customer.beautician = '';
      data.createCustomerModalSuccess = false;
      createCustomerModal.show();
    }

    /** 客戶搜尋 - 清除 */
    const customerDataReset = () => {
      data.customer = {
        name: '',
        tel: '',
        birth: '',
        beautician: '',
      };
    }

    /** 客戶查詢 - 建立客戶資料 */
    const createCustomer = async() => {
      const postData = {
        name: data.customer.name,
        birthday: data.customer.birth,
        cellPhone: data.customer.tel,
        beautician: data.customer.beautician.id
      }

      const url = `/api/Customer`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'POST',
        body: JSON.stringify(postData),
      }

      const response = await fetchApi(url, options);

      updateMessage(response.message);

      if (response.statusCode >= 400) return null;

      data.createCustomerModalSuccess = true;
      data.modalSelectedCustomer = {
        ...postData,
        id: response.data,
        beauticianNickname: data.customer.beautician.id ? beauticianAAData.value.find(item => item.id === data.customer.beautician.id).nickname : null
      };
    }

    /** 取得治療部位 */
    const getBodyPart = async(courseId) => {

      const url = `/api/BodyPart/GetBodyPartByCourse?courseId=${courseId}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      return response.data;
    }

    const setPeriod = (attributes) => {
      const newValue = !appointmentState.filterQueryStatus[attributes];

      appointmentState.filterQueryStatus[attributes] = newValue;

      if (appointmentState.appointmentContent === 'dateOfAppointment') {
        getDaysOutpatientData();
      }
    }

    /** 過濾查詢 - 重置 */
    const filterQueryReset = () => {
      appointmentState.filterQueryStatus = {
        continuousOutpatientNum: 1,
        isAppointment: true,
        morning: true,
        afternoon: true,
        evening: true,
      }

      data.continuousOrReservedStatus = 'continuousOutpatientAmount';

      if (appointmentState.appointmentContent === 'monthOfAppointment') {
        continuousOrReservedToggle(data.continuousOrReservedStatus);
        
      }

      if (appointmentState.appointmentContent === 'dateOfAppointment') {
        getDaysOutpatientData();
      }
    }

    // 手風琴 accordion

    /** accordion data */
    const accordion = ref({
      toggleCard: [
        { 
          className: '.toggle-card',
          maxHeight: '300px'
        },
        { 
          className: '.toggle-card-icon',
          style: 'transform',
          open: 'rotate(180deg)',
          close: 'rotate(0deg)'
        }
      ],
      toggleMore: [
        { 
          className: '.toggle-more',
          maxHeight: '300px'
        },
        { 
          className: '.toggle-more-border',
          style: 'border',
          open: '1px solid #06333B',
          close: '1px solid #DCDCDC'
        }
      ],
      toggleAppointment: [
        { 
          className: '.toggle-appointment',
          maxHeight: null
        },
        { 
          className: '.toggle-appointment-icon',
          style: 'transform',
          open: 'rotate(180deg)',
          close: 'rotate(0deg)'
        }
      ],
    })

    /** accordion */
    onMounted(() => {
      accordionToggle(0, accordion.value.toggleCard);
    })

    // 候補 alternate

    /** 確定候補完成 */
    const alternateSuccessEnter = async() => {
      const url = `/api/Appointment/UpdateAlternateToNormal`;

      const { outpatientId, time, clinicPeriod } = data.appointmenttime;

      data.editAlternateModalData.appointment.time = time;
      data.editAlternateModalData.appointment.timeRange = data.timeRange;
      data.editAlternateModalData.appointment.clinicPeriod = clinicPeriod;
      
      const { appointment } = data.editAlternateModalData;

      const putData = {
        outpatientId: outpatientId,
        appointment,
        appointmentBodyParts: data.appointmentBodyParts.map(item => item.id),
        appointmentOtherParts: data.otherBodyParts,
      }

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'PATCH',
        body: JSON.stringify(putData),
      }

      const response = await fetchApi(url, options);

      updateMessage(response.message);

      if (response.statusCode >= 400) return null;

      if (appointmentState.appointmentContent === 'dateOfAppointment') {
        findAlternateByDate();
      } else {
        findAlternateByYearMonth();
      }

      continuousOrReservedToggle(appointmentState.calendarContent);
      successModal.hide();
      data.appointmenttime = {};
      data.timeRange = '';
    }

    /** 候補成功 - 返回 */
    const turnBackToEditAlternateModal = () => {
      successModal.hide();
      editAlternateModal.show();
    }

    /** 改變醫生或日期，去取時間 */
    const changedateAndOperator = async() => {

      const {departmentId, date, operator} = data.postModalData.appointment;

      const url = `/api/Outpatient/GetWhenCanReserveAppointment?departmentId=${departmentId}&date=${date}&operatorId=${operator}`;
      
      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      data.modalData.outpatientTimes = response.data;
    }

    const updateBasicDataAndFeeData = ref([]);

    /** 更新病歷基本資料和收費 */
    async function updateBasicDataAndFee(customerId, courseId) {
      const url = `/api/MedicalRecord/GetOtherPartHistory?customerId=${customerId}&courseId=${courseId}&limit=5`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      updateBasicDataAndFeeData.value = response.data;
    }

    /** 選擇病歷基本資料和收費 */
    function selectUpdateBasicDataAndFeeData(item) {
      data.otherBodyPartLength++;
      data.otherBodyParts.push({
        part: item.part,
        scope: item.scope,
        price: item.price
      })
    }

    onMounted(() => {
      customerListModal = new Modal(customerListRef.value);
      createCustomerModal = new Modal(createCustomerRef.value);
      appointmentSearchModal = new Modal(appointmentSearchRef.value);
      editAlternateModal = new Modal(editAlternateRef.value);
      successModal = new Modal(successModalRef.value);
      courseModal = new Modal(courseModalRef.value);
      courseAppointmentModal = new Modal(courseAppointmentModalRef.value);

      modal = new Modal(modalRef.value);
    })

    return {
      data,
      accordion,
      showOrHideDoctorData,
      selectCustomer,
      createCustomer,
      accordionToggle,
      customerDataReset,
      appointmentSearch,
      // continuousOrReservedStatus,
      continuousOutpatientNum,
      setPeriod,
      clinicPeriodToCh,
      convertTimeFormatDeleteSecond,
      convertTimeFormatSlash,
      openEditAlternateModal,
      changeCourseType,
      openAppointmentBodyPartsWindow,
      deleteAppointmentBodyPartsItem,
      otherBodyPartJudge,
      openHistoryCourseWindow,
      deleteOtherBodyPartLength,
      addOtherBodyPartLength,
      selecttime,
      openSuccessModal,
      changeCourseId,
      changeCourseId2,
      editAlternateEnter,
      departmentToggle,
      filterQueryReset,
      courseEnter,
      numberGuards,
      toCourse,
      openCourseModal,
      toCourseAlternate,
      turnBackToEditAlternateModal,
      alternateSuccessEnter,
      // openEditModal,
      openEditOutpatient,
      selectCourseDisabledJudge,
      toEditedOutpatient,
      selectCourseDisabledJudge2,
      changedateAndOperator,
      // ref
      customerListRef,
      createCustomerRef,
      appointmentSearchRef,
      editAlternateRef,
      successModalRef,
      courseModalRef,
      modalRef,
      courseAppointmentModalRef,
      displayConditions,
      openAppointmentCourseModal,
      returnToAppointmentSearch,
      changeContinuousOrReservedStatus,
      // modal
      openCustomerListModal,
      openCreateCustomerModal,

      // 更新病歷基本資料和收費
      updateBasicDataAndFeeData,
      selectUpdateBasicDataAndFeeData,

      customerTreatmentHistoryCustomerId,

      beauticianAAData,

      ...toRefs(appointmentState),
    }
  }
}
</script>

