      z-index: 10;
      display: none;
    }

    &:hover img {
      filter: blur(2px);
    }
  }

  .otherBodyPartTable {
    width: 100%;

    td, th {
      padding: 8px 20px;
    }

    th {
      border-bottom: 1px solid #A9A9A9;
